*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
}

.Footer{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 75px;
    background-color: #00828c;
    color: rgb(245, 236, 244);

}
.Footer-link{
    display: flex;
    justify-content: center;
    list-style: none;
    width: 75%;
   

}
.Footer ul li a{
    text-decoration: none;
    color: rgb(11, 12, 14);
    font-weight: bold;
    padding: 10px;
}
.Footer ul li  :hover{
    color: bisque;
}


.Signup-para{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 75vh;
}

.About-para{
    margin: 30px;
    padding: 30px;
    min-height: 75vh;
    height: 2px;
    
}
.Skills-para{
    margin: 30px;
    padding: 30px;
    min-height: 75vh;
}
.Contact-para{
    margin: 30px;
    padding: 30px;
    min-height: 75vh;   
}
.Home-para{
    margin: 30px;
    padding: 30px;
    min-height: 75vh; 
    margin-top: 20px;
    padding-top: 125px;
      
}
.profile{
    
    width: 300px;
    height: 300px;
