*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
.navbar{
       display: flex;
       justify-content: space-around;
       align-items: center;
       height: 75px;
       background-color: #00828c;
       color: rgb(245, 236, 244);
       margin-bottom: 50px;

}
.logo{
   font-size: 30px;
}
.nav-links{
    display: flex;
    justify-content: flex-end;
    list-style: none;
    width: 75%;
}


.Home,
.Contact,
.Skills,
.About,
.Signup {
    text-decoration: none;
    color: aliceblue;
    font-size: 20px;
    padding: 10px 20px;
    margin: 0 10px;
    cursor: pointer;
}

.Signup{
    border: 1px solid yellow;
    border-radius: 5px;

}

.Mobile-menu-icon {
    display: none ;
}

@media screen and (max-width: 780px) {
    .logo{
        display: flex;
        position: absolute;
        top:15px;
        left:35px;

    }
    .nav-links{
        display:none ;
    }
    .nav-links-mobile{
        position: absolute;
        display: block;
        list-style: none;
        background-color: #005469;
        left: 0;
        top: 75px;
        transition: all 0.5s ease-out;
        width: 100%;
    }
    .Home,
    .About,
    .Contact,
    .Skills,
    .Signup{
        color: azure;
        text-align: center;
        padding: 32px;
        width: 100%;
        transition: all 0.5s ease;

    }
    .Signup{
        border: none;
    }
    .Mobile-menu-icon{
        display: block;
        position: absolute;
        font-size: 30px;
        color:white;
        background-color: #00828c;
        border: none;
        outline: none;
        top: 15px;
        right: 25px ;
    }

}